.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Poppins", sans-serif;
  src: url("../../../public/fonts/Poppins/Poppinsregular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Poppins", sans-serif;
  src: url("../../../public/fonts/Poppins/Poppinspoppins-semiboldmedium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Poppins", sans-serif;
  src: url("../../../public/fonts/Poppins/Poppinspoppins-boldsemibold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}
.list-none {
  list-style: none;
  padding: 0;
  margin: 0;
}
.arial {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
.page {
  height: 100%;
}
.dashboard-page {
  min-height: 100%;
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  background-color: #ffffff;
  color: #000000;
  display: flex;
}
.dashboard-header-container {
  padding: 36px 28px 76px 28px;
  background: #ffffff;
  box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
  box-sizing: border-box;
  z-index: 1;
  transition: 1.5s;
}
@media (max-width: 899px) {
  .dashboard-header-container {
    position: absolute;
  }
}
@media (min-width: 900px) {
  .dashboard-header-container {
    max-width: 220px;
  }
}
@media (min-width: 1296px) {
  .dashboard-header-container {
    max-width: none;
  }
}
@media (max-width: 899px) {
  .dashboard-header-container--closed {
    left: -250px;
  }
}
@media (max-width: 899px) {
  .dashboard-header-container--opened {
    height: 100%;
    left: 0;
  }
}
.main-header__toggle {
  border: none;
  background: none;
  position: relative;
  margin-top: 3px;
  cursor: pointer;
}
@media (min-width: 900px) {
  .main-header__toggle {
    display: none;
  }
}
@media (max-width: 899px) {
  .main-header__toggle--opened::before,
  .main-header__toggle--opened::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 15px;
    width: 36px;
    height: 2px;
    background-color: #000000;
    z-index: 2;
  }
  .main-header__toggle--opened::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-header__toggle--opened::after {
    transform: rotate(-45deg);
  }
  .main-header__toggle--closed::before {
    content: "";
    width: 34px;
    height: 28px;
    position: absolute;
    background: url("../img/sandwich.svg") no-repeat;
    background-size: contain;
    left: 35px;
    top: 20px;
  }
}
.dashboard-header__logo-container {
  display: flex;
  align-items: flex-end;
  margin-bottom: 46px;
  max-width: 100%;
}
.dashboard-header__version {
  font-size: 10px;
  line-height: 15px;
  letter-spacing: -0.01em;
  color: #838383;
  padding-bottom: 6px;
  padding-left: 5px;
}
.dashboard-menu {
  margin-bottom: 587px;
}
@media (max-width: 899px) {
  .dashboard-menu {
    margin-bottom: 450px;
  }
}
.dashboard-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.dashboard-menu__item {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 11px 0;
  padding-left: 11px;
  position: relative;
  border-radius: 8px;
}
.dashboard-menu__item:not(:last-child) {
  margin-bottom: 18px;
}
.dashboard-menu__item::after {
  content: "";
  position: absolute;
  right: 8px;
  top: 15px;
  width: 16px;
  height: 16px;
  background: url("../img/arrow.svg") no-repeat center center;
}
.dashboard-menu__item--active {
  background: #5932ea;
}
.dashboard-menu__item--active::after {
  background-image: url("../img/arrow--active.svg");
}
.dashboard-menu__item:not(.dashboard-menu__item--active):hover {
  background-color: #d9d9d9;
}
@media (min-width: 900px) {
  .dashboard-menu__item {
    min-width: none;
  }
}
@media (min-width: 1296px) {
  .dashboard-menu__item {
    min-width: 250px;
  }
}
.dashboard-menu__link {
  display: block;
  margin-left: 14px;
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  text-decoration: none;
  color: #9197b3;
}
.dashboard-menu__link--active {
  color: #ffffff;
}
.dashboard-menu__icon {
  min-width: 24px;
  min-height: 24px;
}
.account {
  display: flex;
  align-items: center;
}
.account__img {
  margin-right: 12px;
}
.account__text {
  margin: 0;
}
.account__name {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.01em;
}
.account__job-position {
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.01em;
  color: #757575;
}
.dashboard-main {
  padding: 20px 20px 0 20px;
}
@media (max-width: 899px) {
  .dashboard-main {
    padding-top: 40px;
    margin: 0 auto;
  }
}
@media (min-width: 900px) {
  .dashboard-main {
    padding: 30px 30px 0 45px;
  }
}
@media (min-width: 1296px) {
  .dashboard-main {
    padding: 41px 48px 0 71px;
  }
}
.dashboard-main__hello-message {
  font-size: 24px;
  line-height: 36px;
  margin: 0;
  margin-bottom: 64px;
}
@media (max-width: 899px) {
  .dashboard-main__hello-message {
    margin-left: 45px;
  }
}
.dashboard-main__content-container {
  min-width: fit-content;
  width: 67.6vw;
  box-sizing: border-box;
  padding: 29px 11px 41px 36px;
  background: #ffffff;
  box-shadow: 0 10px 60px rgba(226, 236, 249, 0.5);
  border-radius: 30px;
  position: relative;
}
.dashboard-main__content-container::before {
  content: "";
  position: absolute;
  width: 100%;
  border: 1px solid #eeeeee;
  top: 165px;
  left: 0;
}
@media (max-width: 899px) {
  .dashboard-main__content-container {
    padding: 29px 20px 41px 20px;
    width: 90vw;
  }
}
@media (min-width: 900px) {
  .dashboard-main__content-container {
    margin-left: 25px;
    max-width: 67.6vw;
  }
}
@media (min-width: 1296px) {
  .dashboard-main__content-container {
    margin-left: 47px;
  }
}
.dashboard-main__top-info-container {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.dashboard-main__title {
  margin: 0;
  margin-bottom: 7px;
  font-weight: 600;
  font-size: 22px;
  line-height: 33px;
  letter-spacing: -0.01em;
}
.dashboard-main__filter {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #16c098;
}
.dashboard-main__search {
  cursor: pointer;
  min-width: 216px;
  border-radius: 10px;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.01em;
  padding: 10px 20px 10px 40px;
  box-sizing: border-box;
  border: none;
  background: #f9fbff url("../img/search.svg") no-repeat 8px center;
}
.dashboard-main__search::placeholder {
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.01em;
  color: #b5b7c0;
}
.dashboard-main__search:hover {
  outline: 1px solid #000000;
}
.dashboard-main__buttom-info-container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.dashboard-main__nav {
  margin-right: 33px;
}
.dashboard-main__page-counter {
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #b5b7c0;
  margin: 0;
}
.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: 271px;
  justify-content: space-between;
}
.nav__button {
  width: 25px;
  height: 25px;
  border: none;
  background: none;
  font-size: 12px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: #404b52;
  position: relative;
  box-sizing: border-box;
  cursor: pointer;
}
.nav__button:not(.nav__button--points) {
  background: #f5f5f5;
  border: 1px solid #eeeeee;
  border-radius: 4px;
}
.nav__list .nav__button--active {
  background: #5932ea;
  border: 1px solid #5932ea;
  color: #ffffff;
}
.nav__button--arrows::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 12px;
  top: 5px;
  left: 7px;
  background: url("../img/arrow-for-button.svg") no-repeat center center;
}
.nav__button:hover {
  transition: 0.3s;
  transform: scale(1.15);
}
.nav__button--forward {
  transform: rotate(180deg);
}
.nav__button--forward:hover {
  transform: rotate(180deg) scale(1.15);
}
.customers-table {
  min-width: 100%;
  text-align: left;
  position: relative;
  margin-bottom: 30px;
}
@media (max-width: 899px) {
  .customers-table {
    border-spacing: 5px;
  }
}
.customers-table__title {
  padding-bottom: 14px;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #b5b7c0;
}
@media (max-width: 899px) {
  .customers-table__title {
    font-size: 12px;
    line-height: 17px;
  }
}
.customers-table__ceil {
  border-bottom: 1px solid #eeeeee;
  padding: 18.2px 0;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #292d32;
  width: max-content;
}
@media (max-width: 899px) {
  .customers-table__ceil {
    font-size: 12px;
    line-height: 17px;
  }
}
.customers-table__ceil-status {
  background: rgba(22, 192, 152, 0.38);
  border: 1px solid #00b087;
  border-radius: 4px;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #008767;
  max-height: 29px;
  height: 29px;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.customers-table__ceil-status--inactive {
  background: #ffc5c5;
  border: 1px solid #df0404;
  color: #df0404;
}
